  background: #ccc;
}
/*标题*/
.add_title[data-v-1880a25f]{
  width: 100%;
  height: 100px;
  line-height: 100px;
  text-align: center;
  font-size: 24px;
  letter-spacing: 10px;
}


/*选项卡*/
.choose_type[data-v-1880a25f]{
  height: 65px;
  width: 100%;
}
.choose_type ul[data-v-1880a25f]{
  border-top: 2px solid #ccc;
  width: 100%;
}
.choose_type ul li[data-v-1880a25f]{
  height: 65px;
  line-height: 50px;
  margin-left: 30px;
