}
/*特产*/
.special[data-v-1880a25f]{
  width: 100%;
}
.special ul[data-v-1880a25f]{
  width: 98%;
  margin-left: 2%;
}
.special_ed[data-v-1880a25f],.special_ing[data-v-1880a25f]{
  width: 23%;
