  padding: 5px 10px;
  background: #F2F2F2;
  border-radius: 3px;
  margin-left: 33px;
  border: 2px solid #F2F2F2;
}
.special_ing[data-v-1880a25f]{
  border: 2px solid #f2f2f2;
