  border-radius: 4px;
}
.sight_pic button[data-v-1880a25f],.sight_vid button[data-v-1880a25f]{
  width: 43%;
  margin: 129px 30%;
  height: 40px;
  line-height: 34px;
  text-align: center;
  background: #f2f2f2;
  border-radius: 3px;
  border: 1px solid #f2f2f2;
}




